.slider, .slider-flipped {
    display: flex;
    align-items: center;
    padding: 20px;
    width: 100%;
    opacity: 0;
    transform: scale(85%);
    transition: 1.2s;
}

.slider-in-view, .slider-flipped-in-view {
    display: flex;
    align-items: center;
    padding: 20px;
    width: 100%;
    opacity: 1;
    transform: scale(100%);
    transition: 1.2s;
}

.slider-flipped, .slider-flipped-in-view {
    flex-direction: row-reverse;
}

.slider-image {
    width: 60%;;
}

.slider-content {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.slider-title {
    padding: 0 0 10px;
}

@media (max-width: 760px) {
    .slider, .slider-flipped, .slider-in-view, .slider-flipped-in-view{
        flex-direction: column;
    }

    .slider-content {
        background-color: #eee;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
    }

    .slider-image {
        width: 100%;
    }
}